import React from 'react'

const ItemsButton = ({filterItem, catItems}) => {  // simply destructing because it is a seprete component used in photo.jsx
    return (
        <>
            <div className="menu-tabs container">
                <div className="menu-tab d-flex justify-content-around">
                  {/* buttono ko fetch kr raha hu uske category ke anusar   */}
                  {/* catitems ke pas savhi categgory values hai  */}
                  {/* onclick krke function ko dusre jagah use krne wala hu par use app pr hi krunga  */}

                  {/* filter item function ko banaya gya itemsbutton mai lekin use ho raha hai photo mai  */}

                  {/* map method hamesa return krata hai kuch to  */}
                {
                    catItems.map((curClem,index) =>{
                        return  <button className="bti" key={index} onClick={() => filterItem(curClem)}>{curClem} </button>
                    })
                }
                   {/* ABOVE METHOD IS TO DISPLAY THE THE BUTTONS   */}
                    {/* <button className="btn btn-warning" onClick={() => filterItem("breakfast")}>Braekfast</button>
                    <button className="btn btn-warning" onClick={() => filterItem("lunch")}>Lunch</button>
                    <button className="btn btn-warning" onClick={() => filterItem("evening")}>Evening</button>
                    <button className="btn btn-warning" onClick={() => filterItem("dinner")}>Dinner</button> */}
                    {/* <button className="btn btn-warning" onClick={() => setItems(Menu)}>All</button> */}
                </div>
            </div>
        </>
    )
}

export default ItemsButton
