import React, { useState } from 'react';
import '../Tabmenu/tab.css';
import Menu from './Menu';
import Menuitems from './Menuitems';
import ItemsButton from './ItemsButton';
//allCatValues is a type of array basically
const allCatValues = [...new Set(Menu.map((CurrElem) => CurrElem.category)),"all"]; // savhi ko karana hai category jitne vi hai  || pehle unique value ko prapt kro phir array mai convert kr lo
console.log(allCatValues);
const Photo = () => {
    const [items , setItems] = useState(Menu);
    const [catItems , setCatItems] = useState(allCatValues);
    const [category,setCategory] = useState('');
// filter items take an arguments

    const filterItem = (Cate) =>{
        if(Cate === "all"){
            setItems(Menu);
            return;
        }

        const newItem = Menu.filter((Curr) =>{
              return Curr.category === Cate;
        });

        setItems(newItem);
    }

    const update = (category) =>{
        if(category === "all")
        {
            setItems(Menu);
            return;
        }
        else
        {
            const newItem = Menu.filter((Curr) =>{
                return Curr.name === category
          });
    
          setItems(newItem);
        }
        
    }

    // menuItems mai sirf ap item ko lo successfully
    return (
       <>
         <h1 className="mt-5 text-center main-heading">Order Your Favourite Dish</h1>
         
           <input onKeyUp={() => update(category)} type="text" placeholder='Enter your dish by category and Name' name='category' 
           value={category} onChange={(e) => setCategory(e.target.value)} />
           
       
          
         <hr style={{borderTop:'10px',margin:'20px auto',width:'80vw',display:'block'}} />

      
        <ItemsButton 
        filterItem = {filterItem}
        catItems = {catItems}
        />
         {/* my main items section  */}
         <Menuitems items = {items}/>
        
       </>
    )
}

export default Photo
